
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.inline-block {
  display: inline-block;
}
.inline-block-mid {
  display: inline-block;
  vertical-align: middle;
}
@keyframes flash {
40% {
    opacity: 1;
}
65% {
    opacity: 0.6;
}
100% {
    opacity: 1;
}
}
.bg-success {
  background-color: #62c278 !important;
}
.bg-success-light {
  background-color: #92d19e !important;
}
.bg-danger {
  background-color: #d1767e !important;
}
.box-shadow-bottom-1 {
  box-shadow: 0 2px 5px 5px rgba(0, 0, 0, 0.08);
  position: relative;
  z-index: 1;
}
div.force .category-failure,
div.force .category-mixed {
  background-color: #dff3db !important;
}
div.force .text-danger {
  color: #28a745 !important;
}
div.force .bg-danger {
  background-color: #92d19e !important;
}
